import './styles.sass'

document.getElementById('app').innerHTML = `<p>Click ðŸ‘† this button</p>`

const clearWaiting = () => {
  document.getElementById('app').innerHTML = '';
};

const countPizzaEaters = (peopleList) => {
  let counter = 0;
  peopleList.forEach((element) => {
    if (element.eatsPizza) {
      counter++;
    }
  })
  return counter;
}

const dividePizza = (personsCount) => {
  let angleStep = 360 / personsCount;
  for (let i = 1; i <= personsCount / 2; i++) {
    let div = document.createElement('div');
    div.className = 'slice';
    div.style.transform = `rotate(${angleStep * i}deg)`;
    document.getElementsByClassName('slices')[0].append(div);
  }
}

const displayPizza = () => {
  document.getElementsByClassName('pizza-container')[0].style.display = 'flex';
}

document.getElementById('load-btn').addEventListener('click', () => {
  document.getElementById('app').innerHTML = 'waiting...'

  fetch('https://gp-js-test.herokuapp.com/pizza')
    .then((response) => response.json())
    .then((data) => {
      clearWaiting();
      displayPizza();
      const counter = countPizzaEaters(data.party);
      dividePizza(counter);
    })
});